.secondSection {
    position: relative;
    width: 100%;
    height: 100vh;
    background-color: black; /* 검은 배경 */
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.secondSectionText {
    font-size: 8rem;
    color: rgba(255, 255, 255, 1); /* 기본 글자색은 흰색 */
    animation: textFadeIn 10s ease forwards, textFlicker 10s infinite; /* 일렁거리는 효과 추가 */
}

/* 별 생성 */
.stars {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: black;
    z-index: -1; /* 텍스트 뒤에 위치 */
    overflow: hidden;
}

@keyframes starsMove {
    from {
        transform: translateY(0);
    }
    to {
        transform: translateY(-1000px); /* 별이 위로 이동하는 애니메이션 */
    }
}

/* 별들 만들기 */
.star {
    position: absolute;
    width: 2px;
    height: 2px;
    background: white;
    border-radius: 50%;
    animation: starBlink 5s infinite ease-in-out;
    animation: starsMove 20s linear infinite;
}

/* 별 깜빡이는 효과 */
@keyframes starBlink {
    0%, 100% {
        opacity: 0.7;
    }
    50% {
        opacity: 1;
    }
}